// src/app/hostparty/page.tsx
"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { createClient } from "@/utils/supabase/client";
import Sidebar from "@/components/sidebar";

export default function HostPartyPage() {
  const supabase = createClient();
  const router = useRouter();

  const [partyName, setPartyName] = useState("");
  const [partyLocation, setPartyLocation] = useState("");
  const [partyTime, setPartyTime] = useState("");
  const [userId, setUserId] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);

  // âœ… Fetch the logged-in user when page loads
  useEffect(() => {
    const getUser = async () => {
      const { data, error } = await supabase.auth.getUser();
      if (error) {
        console.error("Error fetching user:", error);
        return;
      }
      if (data?.user) {
        setUserId(data.user.id);
      }
    };
    getUser();
  }, [supabase]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!userId) {
      alert("You must be logged in to host a party.");
      return;
    }

    setLoading(true);

    const { error } = await supabase.from("parties").insert([
      {
        host_id: userId,
        name: partyName,
        location: partyLocation,
        time: partyTime,
      },
    ]);

    setLoading(false);

    if (error) {
      console.error("Error creating party:", error);
      alert("Something went wrong while creating the party.");
    } else {
      alert("Party hosted successfully!");
      router.push("/dashboard");
    }
  };

  return (
    <div className="ml-16 p-6">
      <Sidebar />
      <h1 className="text-3xl font-bold mb-6">Host a Ride</h1>
      <form
        onSubmit={handleSubmit}
        className="bg-card p-6 rounded-lg shadow-md space-y-4 max-w-md"
      >
        <div>
          <label className="block text-sm font-medium mb-1">Party Name</label>
          <input
            type="text"
            value={partyName}
            onChange={(e) => setPartyName(e.target.value)}
            required
            className="w-full border rounded-lg p-2"
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Location</label>
          <input
            type="text"
            value={partyLocation}
            onChange={(e) => setPartyLocation(e.target.value)}
            required
            className="w-full border rounded-lg p-2"
          />
        </div>

        <div>
          <label className="block text-sm font-medium mb-1">Time</label>
          <input
            type="datetime-local"
            value={partyTime}
            onChange={(e) => setPartyTime(e.target.value)}
            required
            className="w-full border rounded-lg p-2"
          />
        </div>

        <button
          type="submit"
          disabled={loading}
          className="w-full bg-primary text-primary-foreground font-semibold py-2 rounded-lg hover:bg-primary/90 transition-colors"
        >
          {loading ? "Hosting..." : "Host Party"}
        </button>
      </form>
    </div>
  );
}
